@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;600&display=swap');

body, html {
    font-family: 'Poppins', sans-serif;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    height: 200vh;
}

.navigation-wrapper {
  position: fixed;
  top: 0;
  width: 100%;
}

.navigation {
  font-family: 'Poppins';
  height: 88px;
  max-width: 100%;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 150px;
  letter-spacing: 0.38px;
}

.scroll-navigation {
  background-color: white !important;
}

.scroll-navigation > .navigation-logo {
  background-image: url("./assets/black_logo.png");
}

.scroll-navigation .links > a {
  color: #292929 !important;
}

.navigation-logo {
  width: 150px;
  height: 50px;
  padding-right: 150px;
  background-image: url("./assets/white_logo.png");
  background-repeat: no-repeat;
}

.navigation .links {
  display: flex;
  justify-content: space-between;
  width: 50%;
}

.navigation .links > a {
  font-family: 'Poppins';
  font-weight: 600;
  font-size: 15px;
  line-height: 24px;
  color: white;
  text-decoration: none;
  border-bottom: 2px solid transparent;
}

.navigation .links > a:hover {
  border-bottom: 2px solid #EF27A6;
}